export default "import path from 'path'\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop(),\n}\n\ndemo.htmlSource = `\n<form class=\"form-inverse\"\n  style=\"padding: 1rem 1.5rem; background: #0078ff;\">\n  <div class=\"form-group\"\n    style=\"padding: 1rem 1.5rem; background: #ffffff;\">\n    <label class=\"form-label form-label-default xt-custom\">\n      Custom\n    </label>\n    <input type=\"text\" class=\"form-item form-item-default xt-custom\" placeholder=\"Input\" required>\n  </div>\n\n  <fieldset>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Letters\n      </label>\n      <input type=\"text\" class=\"form-item\"\n        placeholder=\"Input\" required pattern=\"[A-Za-z]+\">\n    </div>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Custom\n      </label>\n      <input type=\"text\" class=\"form-item demo--validation-custom\"\n        placeholder=\"Input\" required>\n    </div>\n    <script>\n      function validationCustom (e) {\n          var item = e.target\n          var constraints = ['^(CH-)?\\\\\\\\d{4}$', 'Switzerland ZIPs must have exactly 4 digits: e.g. CH-1950 or 1950'];\n          var constraint = new RegExp(constraints[0], '');\n          if (constraint.test(item.value)) {\n              item.setCustomValidity('');\n          } else {\n              item.setCustomValidity(constraints[1]);\n          }\n      }\n      document.querySelectorAll('.demo--validation-custom').forEach(function (item) {\n          item.addEventListener('input', validationCustom);\n      });\n    </script>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Email\n      </label>\n      <input type=\"email\" class=\"form-item\"\n        placeholder=\"Email\" required>\n    </div>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Number\n      </label>\n      <input type=\"number\" class=\"form-item\"\n        placeholder=\"Number\" required min=\"1\" max=\"10\">\n    </div>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        File\n      </label>\n      <input type=\"file\" class=\"form-item\">\n    </div>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Textarea\n      </label>\n      <textarea class=\"form-item\"\n        placeholder=\"Textarea\" required></textarea>\n    </div>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Select\n      </label>\n      <select class=\"form-item\"\n        required>\n        <option selected disabled></option>\n        <option>Test</option>\n        <option>Test</option>\n        <option>Test</option>\n      </select>\n    </div>\n\n    <div class=\"form-group\">\n      <label class=\"form-label\">\n        Select multiple\n      </label>\n      <select class=\"form-item\"\n        multiple required>\n        <option>Test</option>\n        <option>Test</option>\n        <option>Test</option>\n      </select>\n    </div>\n\n  </fieldset>\n\n  <fieldset>\n\n    <div class=\"form-group\">\n      <input type=\"checkbox\" id=\"check-validation-inverse\" name=\"check-validation-inverse\"\n        required>\n      <label class=\"form-label\" for=\"check-validation-inverse\">\n        Acconsento allutilizzo dei miei dati in linea con la <a href=\"#\" target=\"_blank\">privacy policy</a>\n      </label>\n    </div>\n\n  </fieldset>\n\n  <fieldset>\n\n    <div class=\"form-group\">\n      <input type=\"radio\" id=\"radio-validation-inverse-0\" name=\"radio-validation-inverse\"\n        required>\n      <label class=\"form-label\" for=\"radio-validation-inverse-0\">\n        Radio 0\n      </label>\n    </div>\n\n    <div class=\"form-group\">\n      <input type=\"radio\" id=\"radio-validation-inverse-1\" name=\"radio-validation-inverse\"\n        required>\n      <label class=\"form-label\" for=\"radio-validation-inverse-1\">\n        Radio 1\n      </label>\n    </div>\n\n  </fieldset>\n\n  <fieldset>\n\n    <button type=\"submit\" class=\"btn btn-primary btn-big btn-tall\">\n      Invia\n    </button>\n\n  </fieldset>\n</form>\n`\n"